import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  title = 'my-portfolio';
  bodyOpen = true;
  contactOpen = false;
  aboutOpen = false;
  openAbout(){
    this.bodyOpen = false;
    this.contactOpen = false;
    this.aboutOpen = true;
  }
  openContact(){
    this.bodyOpen = false;
    this.contactOpen = true;
    this.aboutOpen = false;
  }
}
